



























import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class PublicVote extends Vue {
  @Prop({ default: [] }) seats!: any[];
  text = "public vote";
  selectedNo = -1;

  vote(seatNo: number) {
    this.selectedNo = seatNo;
    this.$socket.werewolves.emit("publicvote", { seatNo });
  }
}
