

















import { Component, Prop, Vue } from "vue-property-decorator";
import Card from "@/models/card";

@Component
export default class PeerCard extends Vue {
  @Prop() card!: string;

  getCardText() {
    switch (this.card) {
      case "wolf":
        return "狼人";
      case "folk":
        return "平民";
      case "seer":
        return "預言家";
      case "witch":
        return "女巫";
      default:
        return "";
    }
  }
}
